/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * This class creates a link (waved color-filled line) between two nodes in a
 * Sankey Diagram.
 * 
 * @see {@link ISankeyLinkEvents} for a list of available events
 * @see {@link ISankeyLinkAdapters} for a list of available Adapters
 * @important
 */
export class SankeyLink extends Sprite {

	/**
	 * Defines available properties.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyLinkProperties}
	 */
	public _properties: ISankeyLinkProperties;

	/**
	 * Defines available adapters.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyLinkAdapters}
	 */
	public _adapter: ISankeyLinkAdapters;

	/**
	 * Event dispatcher.
	 * 
	 * @type {SpriteEventDispatcher<AMEvent<SankeyLink, ISankeyLinkEvents>>} Event dispatcher instance
	 */
	public events: SpriteEventDispatcher<AMEvent<SankeyLink, ISankeyLinkEvents>>;

	/**
	 * Defines a type of data item used by this class.
	 *
	 * @ignore Exclude from docs
	 * @type {SankeyDiagramDataItem}
	 */
	public _dataItem: SankeyDiagramDataItem;

	/**
	 * A gradiend instance that is used to provided colored gradient fills for
	 * the Sankey link.
	 *
	 * @type {LinearGradient}
	 */
	public gradient: LinearGradient = new LinearGradient();

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.className = "SankeyLink";

		let interfaceColors = new InterfaceColorSet();

		this.element = this.paper.add("path");

		this.startAngle = 0;
		this.endAngle = 0;

		this.linkWidth = 0;

		this.startX = 0;
		this.endX = 0;

		this.startY = 0;
		this.endY = 0;

		this.strokeOpacity = 0.2;
		this.stroke = interfaceColors.getFor("alternativeBackground");

		this.maxWidth = 100;
		this.maxHeight = 100;

		let hoverState = this.states.create("hover");
		hoverState.properties.strokeOpacity = 0.4;

		this.tooltipText = "{fromName}â†’{toName}:{value.value}";

		this.applyTheme();
	}

	/**
	 * (Re)validates (redraws) the link.
	 *
	 * @ignore Exclude from docs
	 */
	public validate() {
		super.validate();

		let x0 = this.startX
		let y0 = this.startY;
		let x1 = this.endX;
		let y1 = this.endY;

		let startAngle = this.startAngle;
		let endAngle = this.endAngle;
		let w = this.linkWidth;

		let path = "";

		this.fill = color();
		this.strokeWidth = w;
		this.element.attr({ "stroke-linecap": "butt" });

		x0 = x0 + w / 2 * $math.sin(startAngle);
		x1 = x1 + w / 2 * $math.sin(endAngle);

		y0 = y0 + w / 2 * $math.cos(startAngle);
		y1 = y1 + w / 2 * $math.cos(endAngle);

		let kx0 = x0 + (x1 - x0) / 2 * $math.cos(startAngle);
		let ky0 = y0 + (y1 - y0) / 2 * $math.sin(startAngle);

		let kx1 = x1 - (x1 - x0) / 2 * $math.cos(endAngle);
		let ky1 = y1 - (y1 - y0) / 2 * $math.sin(endAngle);

		// hack to solve problem of gradients not being applied on straight lines.
		if (x0 == x1) {
			x1 += 0.001;
		}

		if (y0 == y1) {
			y1 += 0.001;
		}

		path = $path.moveTo({ x: x0, y: y0 });
		path += $path.cubicCurveTo({ x: x1, y: y1 }, { x: kx0, y: ky0 }, { x: kx1, y: ky1 });

		this._bbox = {
			x: x0,
			y: y0,
			width: x1 - x0,
			height: y1 - y0
		};		

		this.element.attr({ "d": path });
	}

	/**
	 * [startAngle description]
	 *
	 * @todo Description
	 * @param {number}  value  Start angle
	 */
	public set startAngle(value: number) {
		this.setPropertyValue("startAngle", value, true);
	}

	/**
	 * @return {number} Start angle
	 */
	public get startAngle(): number {
		return this.getPropertyValue("startAngle");
	}

	/**
	 * [endAngle description]
	 *
	 * @todo Description
	 * @param {number}  value  End angle
	 */
	public set endAngle(value: number) {
		this.setPropertyValue("endAngle", value, true);
	}

	/**
	 * @return {number} End angle
	 */
	public get endAngle(): number {
		return this.getPropertyValue("endAngle");
	}

	/**
	 * [startX description]
	 *
	 * @todo Description
	 * @param {number}  value  Start X
	 */
	public set startX(value: number) {
		this.setPropertyValue("startX", value, true);
	}

	/**
	 * @return {number} Start X
	 */
	public get startX(): number {
		return this.getPropertyValue("startX");
	}

	/**
	 * [endX description]
	 *
	 * @todo Description
	 * @param {number} value  End X
	 */
	public set endX(value: number) {
		this.setPropertyValue("endX", value, true);
	}

	/**
	 * @return {number} End X
	 */
	public get endX(): number {
		return this.getPropertyValue("endX");
	}

	/**
	 * [startY description]
	 *
	 * @todo Description
	 * @param {number}  value  Start Y
	 */
	public set startY(value: number) {
		this.setPropertyValue("startY", value, true);
	}

	/**
	 * @return {number} Start Y
	 */
	public get startY(): number {
		return this.getPropertyValue("startY");
	}

	/**
	 * [endY description]
	 *
	 * @todo Description
	 * @param {number} value End Y
	 */
	public set endY(value: number) {
		this.setPropertyValue("endY", value, true);
	}

	/**
	 * @return {number} End Y
	 */
	public get endY(): number {
		return this.getPropertyValue("endY");
	}

	/**
	 * [linkWidth description]
	 *
	 * @todo Description
	 * @param {number} value [description]
	 */
	public set linkWidth(value: number) {
		this.setPropertyValue("linkWidth", value, true);
	}

	/**
	 * @return {number} [description]
	 */
	public get linkWidth(): number {
		return this.getPropertyValue("linkWidth");
	}

	/**
	 * Should link be filled with a solid color or gradient.
	 * 
	 * @param {"solid" | "gradient"}  value  Fill mode
	 */
	public set colorMode(value: "solid" | "gradient") {
		if (value == "gradient") {
			//this.fill = this.gradient;
			this.stroke = this.gradient;
		}
		this.setPropertyValue("colorMode", value, true);
	}

	/**
	 * @type {"solid" | "gradient"} Fill mode
	 */
	public get colorMode(): "solid" | "gradient" {
		return this.getPropertyValue("colorMode");
	}

	/**
	 * Adds color steps in the link gradient.
	 * 
	 * @param {Color | Pattern | LinearGradient | RadialGradient}  value  Fill option
	 */
	protected setStroke(value: Color | Pattern | LinearGradient | RadialGradient) {
		super.setStroke(value)
		if (value instanceof Color) {
			this.gradient.stops.clear();
			this.gradient.addColor(value);
			this.gradient.addColor(value);
		}
	}

	/**
	 * Updates bounding box based on element dimension settings.
	 *
	 * @ignore Exclude from docs
	 */
	public measureElement(): void {

	}	

}

/**
 * Register class in system, so that it can be instantiated using its name from
 * anywhere.
 * 
 * @ignore
 */
system.registeredClasses["SankeyLink"] = SankeyLink;